const handleError = async (e) => {
  var errMsg = "\n";
  var log_post_body = {
    "app": "",
    "record": {
      "ID": {
        "value": kintone.app.getId()
      }
    }
  }

  if (kintone.app.record.getId()) {
    log_post_body["record"]["ID"] = { "value": kintone.app.record.getId() }
  }

  if (e.target && e.target.response) {
    errMsg += "" + e.target.response + "\n";
    if (e.target.status && e.target.statusText) {
      var response = "" + e.target.status + "" + e.target.statusText + "\n";
      log_post_body["record"][""] = { "value": response };
    }
  } else if (e.message) {
    errMsg += "" + e.message + "\n";
    if (e.errors) {
      var messages = "";
      Object.keys(e.errors).forEach(function (key) {
        if (e.errors[key]["messages"]) {
          e.errors[key]["messages"].forEach(function (message) {
            messages += "{" + key + "\n" + message + "}\n";
          })
        }
      })
      errMsg += messages;
    }
  } else if (e.results) {
    e.results.forEach(function (err, index) {
      if (Object.keys(err).length && err.message) {
        errMsg += index + "." + err.message + "\n";
      }
    });
  } else {
    errMsg += e;
  }
  log_post_body["record"][""] = { "value": errMsg };

  if (e.type) {
    errMsg += "" + e.type + "\n";
    log_post_body["record"][""] = { "value": e.type };
  }

  if (e.name) {
    errMsg += "" + e.name + "\n";
    log_post_body["record"][""] = { "value": e.name };
  }

  if (e.fileName && e.lineNumber) {
    errMsg += "" + e.fileName + " " + e.lineNumber + "\n";
    log_post_body["record"][""] = { "value": e.fileName };
    log_post_body["record"][""] = { "value": e.lineNumber }
  }
  if (e.stack) {
    errMsg += "----  ----\n";
    errMsg += e.stack + "\n";
    errMsg += "---------------------------\n";
    log_post_body["record"][""] = { "value": e.stack };
  } else {
    var trace = (new Error('Trace')).stack;
    errMsg += "----  ----\n";
    errMsg += trace + "\n";
    errMsg += "---------------------------\n";
    log_post_body["record"][""] = { "value": trace };
  }
  // 
  kintone.api(kintone.api.url('/k/v1/app', true), 'GET', { "id": kintone.app.getId() }).then(function (appInfo) {
    return EMCloud.modules.getAppsId(appInfo.spaceId)
  }).then(function(appIds) {
    log_post_body["app"] = appIds["EMCsaveLog"];
    return kintone.api(kintone.api.url('/k/v1/record', true), 'POST', log_post_body)
  }).then(function (resp) {
    console.error(errMsg);
    console.log("\nID" + log_post_body["app"]);
    alert("");
  }).catch(function (error) {
    console.error(errMsg);
    console.log(error);
    alert("\n");
  });
}
export {
  handleError
}