import { handleError } from '../logger';
//2022-10-26   
//2022-11-25   
const appCodes = [
    'EMCemployee',
    'EMCdependentExemption',
    'EMCcommuteInfo',
    'EMCbasicInfomation',
    'EMCprocedureManagement',
    'EMCsalaryManagement',
    'EMCpaymentDeduction',
    'EMCcorporationMaster',
    'EMCenvironmentMaster',
    'EMCemployHiring',
    'EMCemployManagement',
    'EMCachievement',
    'EMCtemplateFile',
    'EMCconvertOBCItem',
    'EMCemployProcedure',
    'EMCbonusManagement',
    'EMCemployProcedureMaster',
];

/**
 *
 * ID
 * resp
 * app:{
 *     appCode: appId,
 *     ex)
 *     EMChResources: 999
 * }
 * @param {Number} spaceID EMID
 * @return {Object} ID
 */
const getAppsId = (spaceID) => {
    return new Promise(function (resolve, reject) {
        // ID
        const ids = sessionStorage.getItem('appIds');
        if (ids) {
            const parsed = JSON.parse(ids);
            if (parsed['space'] === spaceID) {
                return resolve(parsed['appIds']);
            } else {
                // 
                sessionStorage.removeItem('appIds');
            }
        }
        const body = {
            id: spaceID,
        };
        kintone.api(
            kintone.api.url('/k/v1/space', true),
            'GET',
            body,
            function (resp) {
                const respAppsID = {};
                appCodes.forEach((appCode) => {
                    for (let i = 0; i < resp.attachedApps.length; i++) {
                        const attachedApp = resp.attachedApps[i];
                        // ID || ID 
                        if (`${appCode}${spaceID}` === attachedApp.code || `${appCode}` === attachedApp.code) {
                            respAppsID[appCode] = attachedApp.appId;
                        }
                    }
                });
                sessionStorage.setItem(
                    'appIds',
                    JSON.stringify({
                        space: spaceID,
                        appIds: respAppsID,
                    })
                );
                return resolve(respAppsID);
            },
            function (error) {
                handleError(error);
                reject(error);
            }
        );
    }).catch(function (error) {
        handleError(error);
        reject(error);
    });
};
export { getAppsId };
